<template>
  <div style="width: 100%">
    <div id="myChart2" :style="{ width: '100%', height: '400px' }"></div>
  </div>
</template>
<script>
import * as echarts from "echarts";
export default {
  name: "bar",
  data() {
    return {};
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById("myChart2"));
      // 绘制图表
      myChart.setOption({
        grid: { show: "true", borderWidth: "0" },
        title: {
          text: "单位:万",
          textStyle: { color: "#516CDC", fontSize: 14, fontWeight: "normal" },
          padding: [5, 0, 0, 0],
        },
        legend: {
          border: "none",
          type: "plain",
          top: 0,
          right: 20,
          itemGap: 64,
          itemWidth: 10,
          icon: "circle",
          selectedMode: false,
          textStyle: { padding: [0, 0, 0, 4] },
          data: ["新增登记", "完成服务"],
        },
        grid: { left: 0, top: 40, bottom: 20, right: 10, containLabel: true },
        xAxis: {
          type: "category",
          data: [
            "12-26",
            "12-27",
            "12-28",
            "12-29",
            "12-30",
            "12-31",
            "01-01",
            "01-02",
            "01-03",
            "01-04",
            "01-05",
            "01-06",
            "01-07",
            "01-08",
            "01-09",
          ],
          axisLine: { lineStyle: { color: "#ccc" } },
          axisTick: { length: 3 },
          axisLabel: { color: "#999" },
          minorSplitLine: {
            show: true,
          },
        },
        yAxis: {
          type: "value",
          axisLine: { show: true, lineStyle: { color: "#ccc" } },
          axisLabel: { color: "#999" },
          splitLine: { show: false },
          minorSplitLine: {
            show: true,
          },
        },
        tooltip: {
          trigger: "axis",
          padding: [12, 17, 20, 23],
          textStyle: { color: "#424242" },
          renderMode: "html",
          className: "tooltip",
        },
        series: [
          {
            type: "line",
            data: [0, 10, 2, 4, 4, 7, 0, 0, 0, 3, 0, 9, 6, 0, 0],
            smooth: true, // 平滑曲线
            showSymbol: false,
            itemStyle: { color: "#126EFC" },
            lineStyle: { width: 3, color: "#126EFC" },
            areaStyle: { color: "rgba(0, 110, 254, 0.1)" },
          },
        ],
      });
      window.onresize = function () {
        myChart.resize();
      };
    },
  },
};
</script>



