<template>
  <div style="width: 100%">
    <div id="myChart4" :style="{ width: '100%', height: '320px' }"></div>
  </div>
</template>
<script>
import * as echarts from "echarts";
export default {
  name: "bar",
  data() {
    return {};
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById("myChart4"));
      // 绘制图表
      myChart.setOption({
        title: {
          text: "单位:万",
          textStyle: { color: "#516CDC", fontSize: 14, fontWeight: "normal" },
          padding: [5, 0, 0, 0],
        },
        tooltip: {
          trigger: "axis",
        },

        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },

        // toolbox: {
        //   feature: {
        //     saveAsImage: {},
        //   },
        // },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          axisLine: { lineStyle: { color: "#ccc" } },
          axisTick: { length: 3 },
          axisLabel: { color: "#999" },
          minorSplitLine: {
            show: true,
          },
        },
        yAxis: {
          type: "value",
          axisLine: { show: true, lineStyle: { color: "#ccc" } },
          axisLabel: { color: "#999" },
          splitLine: { show: false },
          minorSplitLine: {
            show: true,
          },
        },
        series: [
          {
            name: "Email",
            type: "line",
            stack: "Total",
            data: [120, 132, 101, 134, 90, 230, 210],
          },
          {
            name: "Union Ads",
            type: "line",
            stack: "Total",
            data: [220, 182, 191, 234, 290, 330, 310],
          },
          {
            name: "Video Ads",
            type: "line",
            stack: "Total",
            data: [150, 232, 201, 154, 190, 330, 410],
          },
          {
            name: "Direct",
            type: "line",
            stack: "Total",
            data: [320, 332, 301, 334, 390, 330, 320],
          },
          {
            name: "Search Engine",
            type: "line",
            stack: "Total",
            data: [820, 932, 901, 934, 1290, 1330, 1320],
          },
        ],
      });
      window.onresize = function () {
        myChart.resize();
      };
    },
  },
};
</script>



