<template>
  <div style="width: 100%">
    <div id="myChart3" :style="{ width: '100%', height: '300px' }"></div>
  </div>
</template>
<script>
import * as echarts from "echarts";
export default {
  name: "bar",
  data() {
    return {};
  },
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      var colors = ["red", "#80B94D", "#FED349", "#0EB2E5", "#9674FF"];
      var data = [
        { value: 55, name: "5000-8000" },
        { value: 38, name: "3000-4000" },
        { value: 32, name: "4000-5000" },
        { value: 30, name: "1000-3000" },
        { value: 28, name: "0-1000" },
      ];
      var total = data.reduce((p, v) => {
        return p + v.value;
      }, 0);
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById("myChart3"));
      // 绘制图表
      myChart.setOption({
        title: {
          text: "135,52",
          left: "center",
          top: "middle",
          textStyle: {
            fontSize: "24px",
            fontFamily: "Bahnschrift-Bold, Bahnschrift",
            fontWeight: "bold",
            color: "#3D3D3D",
          },
          textAlign: "auto",
        },

        legend: {
          show: true,
        },
        tooltip: {
          show: true,
          trigger: "item",
          formatter: "{b} : {c} ({d}%)",
        },
        color: colors,
        calculable: true,
        series: [
          {
            type: "pie",
            radius: ["30%", "65%"],
            center: ["50%", "50%"],
            roseType: "radius",
            label: {
              show: true,
              color: "inherit",
              position: "outside",
              fontSize: 14,
              formatter: "{b} {d}%",
            },
            labelLine: {
              length: 1,
              length2: 20,
              smooth: true,
            },
            data: data,
          },
        ],
      });
      window.onresize = function () {
        myChart.resize();
      };
    },
  },
};
</script>



